.logo-container {
	display: grid;
	justify-items: center;
	align-self: center;
}

.logo {
	font-size: 5em;
	font-weight: 800;
}

.tagline {
	font-size: 3em;
}

.logo-small {
	font-size: 3em;
	justify-self: left;
}

@media all and (max-width: 500px) {
	.logo-small {
		font-size: 2em;
	}
}
